/* eslint-disable*/
import React, { Fragment, Component } from 'react';
import { FormControl } from '../../../src';
import { FormControlType } from '../../../src/formControl/FormControlType';
import generateState from '../generateState';
import Alert from '../../../src/alert';
import { generateCodeBlock, generateInput, WithDocs } from '../index';

class WithDoc extends Component {
  state = { ...generateState(this.props.knobs) };

  handleOnChange = (value, stateName) => {
    let newValue = value;
    const { onChange } = this.props;

    if (type === 'select' && options) {
      newValue = options.find(option => value === option.value);
    }

    onChange && onChange(newValue);

    this.setState({ [stateName]: newValue });
  };

  getKnobsControl = () => {
    const { placeholder } = this.state;
    const { knobs } = this.props;
    let required;
    let selectedOptions;

    const locale = this.state.locale && this.state.locale.value ? this.state.locale.value : 'en-GB';

    return knobs.knobs.map(({ state, label, type, options }) => {
      required = false;
      selectedOptions = null;

      if (type === FormControlType.SELECT && options) {
        required = true;
        if (this.state[state] && this.state[state].value) {
          selectedOptions = options.find(option => this.state[state].value === option.value);
        }
      }

      let newValue = knobs.defaultState;
      if (this.state[state]) {
        newValue = this.state[state].value;
        if (this.state[state].value) {
          newValue = this.state[state].value;
        }
      }

      return (
        <div className="col-md-6 m-b-2" key={state}>
          <label htmlFor={state} className="control-label">
            {label}
          </label>

          <FormControl
            type={type}
            id={state}
            label={label}
            placeholder={placeholder}
            selectedOption={selectedOptions}
            options={options && options.map(option => ({ ...option }))}
            onChange={value => this.handleOnChange(value, state)}
            name={`docs-${label}`}
            onBlur={() => {}}
            onFocus={() => {}}
            required={required}
            locale={locale}
            value={newValue}
          />
        </div>
      );
    });
  };

  getChildrenState = () => {
    const childrenState = {};

    Object.keys(this.state).forEach(stateName => {
      childrenState[stateName] = this.state[stateName].value || this.state[stateName];
    });

    return childrenState;
  };

  render() {
    const { knobs, componentInfo } = this.props;

    return (
      <div className="container">
        <section className="section">
          <div className="row">
            <div className="col-md-6">
              <h2>{componentInfo.componentName}</h2>
              <p>{componentInfo.componentDescription}</p>
              {generateCodeBlock(componentInfo.componentName, knobs, this.state)}
            </div>

            <div className="col-md-6 m-t-2">
              {React.cloneElement(this.props.children, { ...this.getChildrenState() })}
              {this.getKnobsControl()}
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default WithDoc;



